.page {
  font-family: Cinzel, sans-serif;
}

.page__body {
  margin: 0;
}

.page__body:has(.menu:target) {
  overflow: hidden;
}

.page__menu {
  background-color: #d12d35;
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  transform: translateX(0);
}

.page__section {
  margin-bottom: 100px;
  display: grid;
}

@media (min-width: 744px) {
  .page__section {
    margin-bottom: 140px;
