.menu {
  height: 100vh;
  font-family: Cinzel, sans-serif;
  font-weight: 700;
  display: block;
  overflow-y: auto;
}

.menu__header {
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
}

.menu__logo {
  background-image: url("logo.20a41373.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 87px;
  height: 65px;
  transition: all .2s ease-in-out;
  display: -ms-flexbox;
  display: flex;
}

.menu__logo:hover {
  transform: scale(1.2);
}

.menu__closer {
  display: -ms-flexbox;
  display: flex;
}

.menu__closer-link {
  background-image: url("close-icon.b76693be.png");
  width: 32px;
  height: 32px;
  transition: all .2s ease-in-out;
}

.menu__closer-link:hover {
  transform: scale(1.2);
}

.menu__list {
  margin-top: 40px;
  margin-bottom: 32px;
  padding-left: 0;
  list-style: none;
}

.menu__item {
  width: -moz-fit-content;
  width: fit-content;
  padding-bottom: 32px;
  position: relative;
}

.menu__item:last-child {
  padding-bottom: 0;
}

.menu__item:after {
  transform-origin: 0 0;
  content: "";
  background-color: #fff;
  width: 0;
  height: 2px;
  transition: width .3s, left .3s;
  display: block;
  position: absolute;
  top: 24px;
  left: 50%;
}

.menu__item:hover:after {
  width: 100%;
  left: 0;
}

.menu__link {
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #fff;
  font-size: 22px;
  line-height: 22px;
  text-decoration: none;
}

.menu__contact-block {
  margin-bottom: 16px;
}

.menu__contact-title {
  color: #fff;
  text-transform: uppercase;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 27px;
}

.menu__contact-link {
  color: #fff;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 16px;
  text-decoration: none;
  position: relative;
}

.menu__contact-link:after {
  transform-origin: 0 0;
  content: "";
  background-color: #fff;
  width: 0;
  height: 2px;
  transition: width .3s, left .3s;
  display: block;
  position: absolute;
  top: 24px;
  left: 50%;
}

.menu__contact-link:hover:after {
  width: 100%;
  left: 0;
}

.menu__call-link {
  cursor: pointer;
  border-bottom: 1px solid #fff;
  width: -moz-fit-content;
  width: fit-content;
  padding-bottom: 8px;
  transition: all .2s ease-in-out;
  display: block;
}

.menu__call-link:after {
  display: none;
}

.menu__call-link:hover {
  transform-origin: center;
  text-decoration: none;
  transform: scale(1.2);
}

